'use client';

import { useState, useEffect } from 'react';
import { FaUser, FaEnvelope, FaAddressCard, FaEdit, FaPhone, FaMapMarkerAlt } from 'react-icons/fa';
import { useAlert } from '@/components/ui/customAlert';

interface User {
  id: string;
  firstName: string;
  lastName: string;
  email: string;
  phone?: string;
  address?: string;
  bi?: string; // Adicionando a propriedade bi que estava faltando
}

export default function Perfil() {
  const { showAlert, AlertContainer } = useAlert();
  const [user, setUser] = useState<User | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    // Simular carregamento dos dados do usuário
    const loadUserData = () => {
      try {
        const userData = localStorage.getItem('user');
        if (userData) {
          const parsedUser: User = JSON.parse(userData);
          setUser(parsedUser);
        }
      } catch (error) {
        console.error('Erro ao carregar dados do usuário:', error);
        showAlert('Erro ao carregar perfil', 'error');
      } finally {
        setIsLoading(false);
      }
    };

    loadUserData();
  }, [showAlert]);

  const profileFields = [
    { icon: FaUser, label: 'Nome Completo', value: `${user?.firstName || ''} ${user?.lastName || ''}`.trim() },
    { icon: FaEnvelope, label: 'Email', value: user?.email || 'Não informado' },
    { icon: FaAddressCard, label: 'BI/Identificação', value: user?.bi || 'Não informado' },
    { icon: FaPhone, label: 'Telefone', value: user?.phone || 'Não informado' },
    { icon: FaMapMarkerAlt, label: 'Endereço', value: user?.address || 'Não informado' }
  ];

  const handleEditProfile = () => {
    showAlert('Funcionalidade de edição em desenvolvimento', 'info');
  };

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-orange-500"></div>
      </div>
    );
  }

  if (!user) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <h2 className="text-xl font-semibold text-gray-600 dark:text-gray-300 mb-4">
            Usuário não encontrado
          </h2>
          <p className="text-gray-500 dark:text-gray-400">
            Faça login novamente para acessar seu perfil
          </p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900 py-8">
      <AlertContainer />
      
      <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header */}
        <div className="text-center mb-8">
          <h1 className="text-3xl font-bold text-gray-900 dark:text-white mb-2">
            Meu Perfil
          </h1>
          <p className="text-gray-600 dark:text-gray-300">
            Gerencie suas informações pessoais
          </p>
        </div>

        {/* Profile Card */}
        <div className="bg-white dark:bg-gray-800 rounded-2xl shadow-lg border border-gray-200 dark:border-gray-700 overflow-hidden">
          {/* Profile Header */}
          <div className="bg-gradient-to-r from-orange-500 to-red-600 p-6 text-white">
            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-4">
                <div className="w-20 h-20 bg-white/20 rounded-full flex items-center justify-center backdrop-blur-sm">
                  <FaUser className="w-8 h-8" />
                </div>
                <div>
                  <h2 className="text-2xl font-bold">
                    {user.firstName} {user.lastName}
                  </h2>
                  <p className="text-orange-100">{user.email}</p>
                </div>
              </div>
              
              <button
                onClick={handleEditProfile}
                className="flex items-center space-x-2 bg-white/20 hover:bg-white/30 px-4 py-2 rounded-lg backdrop-blur-sm transition-all duration-200 transform hover:scale-105"
              >
                <FaEdit className="w-4 h-4" />
                <span>Editar</span>
              </button>
            </div>
          </div>

          {/* Profile Details */}
          <div className="p-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              {profileFields.map((field, index) => (
                <div
                  key={index}
                  className="flex items-center space-x-4 p-4 bg-gray-50 dark:bg-gray-700 rounded-lg hover:bg-gray-100 dark:hover:bg-gray-600 transition-colors duration-200"
                >
                  <div className="flex-shrink-0">
                    <field.icon className="w-6 h-6 text-orange-500" />
                  </div>
                  <div className="flex-1 min-w-0">
                    <p className="text-sm font-medium text-gray-500 dark:text-gray-400">
                      {field.label}
                    </p>
                    <p className="text-lg font-semibold text-gray-900 dark:text-white truncate">
                      {field.value}
                    </p>
                  </div>
                </div>
              ))}
            </div>
          </div>

          {/* Additional Actions */}
          <div className="border-t border-gray-200 dark:border-gray-700 p-6 bg-gray-50 dark:bg-gray-700/50">
            <div className="flex flex-wrap gap-4 justify-center">
              <button className="px-6 py-3 bg-orange-500 hover:bg-orange-600 text-white rounded-lg font-medium transition-colors duration-200 transform hover:scale-105">
                Alterar Senha
              </button>
              <button className="px-6 py-3 border border-gray-300 dark:border-gray-600 text-gray-700 dark:text-gray-300 hover:bg-gray-50 dark:hover:bg-gray-600 rounded-lg font-medium transition-colors duration-200">
                Configurações
              </button>
              <button className="px-6 py-3 border border-red-300 text-red-600 dark:text-red-400 hover:bg-red-50 dark:hover:bg-red-900/20 rounded-lg font-medium transition-colors duration-200">
                Sair
              </button>
            </div>
          </div>
        </div>

        {/* Stats Section */}
        <div className="mt-8 grid grid-cols-1 md:grid-cols-3 gap-6">
          <div className="bg-white dark:bg-gray-800 p-6 rounded-2xl shadow-lg border border-gray-200 dark:border-gray-700 text-center">
            <div className="text-2xl font-bold text-orange-500 mb-2">0</div>
            <div className="text-gray-600 dark:text-gray-300">Pagamentos</div>
          </div>
          <div className="bg-white dark:bg-gray-800 p-6 rounded-2xl shadow-lg border border-gray-200 dark:border-gray-700 text-center">
            <div className="text-2xl font-bold text-green-500 mb-2">0</div>
            <div className="text-gray-600 dark:text-gray-300">Recibos</div>
          </div>
          <div className="bg-white dark:bg-gray-800 p-6 rounded-2xl shadow-lg border border-gray-200 dark:border-gray-700 text-center">
            <div className="text-2xl font-bold text-blue-500 mb-2">0</div>
            <div className="text-gray-600 dark:text-gray-300">Documentos</div>
          </div>
        </div>
      </div>
    </div>
  );
}